<template>
    <!-- button container -->
    <div class="button-container auto">

      <!-- label -->
      <div v-if="type === 'next'" class="button-label-next">
        <div class="utility upcase">{{type}}</div>
      </div>

      <div v-else class="button-label-prev">
        <div class="utility upcase">{{type}}</div>
      </div>

      <!-- arrow -->
      <div v-if="type === 'next'" class="button-arrow-next">
        <arrow :type="type"></arrow>
      </div>

      <div v-else class="button-arrow-prev">
        <arrow :type="type"></arrow>
      </div>

      <!-- background next -->
      <div v-if="type === 'next'" class="button-background-next">
        <svg class="button-background-svg" viewBox="0 0 221 48">
          <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
            <g>
              <path class="button-background-path" stroke-width="1" d="M0.5,0.5 L0.5,29.3873894 L19.3036863,47.5 L220.5,47.5 L220.5,0.5 L0.5,0.5 Z"></path>
            </g>
          </g>
        </svg>
      </div>

      <!-- background prev -->
      <div v-else class="button-background-prev">
        <svg class="button-background-svg" viewBox="0 0 220 48">
          <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
            <g id="Button" transform="translate(110.000000, 24.000000) scale(-1, 1) translate(-110.000000, -24.000000) ">
              <path class="button-background-path" stroke-width="1" d="M0.5,0.5 L0.5,29.3873894 L19.3036863,47.5 L219.5,47.5 L219.5,0.5 L0.5,0.5 Z"></path>
            </g>
          </g>
        </svg>
      </div>

    </div>
</template>


<script>
  import arrow from '../atoms/arrow'
  export default {
    props: ['type'],
    components: {
      'arrow': arrow
    }
  }
</script>


<!-- scoped -->
<style scoped lang='scss'>

  //120x36, 24 text margin

  @import '../../styles/variables';

  .button-container {
    position: relative;
    width: 221px;
    height: 48px;
    cursor: pointer;
    &:hover {
      .utility {
        color: $white;
      }
      .button-background-path {
        stroke: $white;
        fill: $blue;
      }
    }
    @media screen and (max-width: $sm-break) {
      width: 120px;
      height: 36px;
    }
  }

  .button-label-next, .button-label-prev {
    position: absolute;
    z-index: 2;
    top: 0;
    margin-top: 14.5px;
    .utility {
      transition: all $transition-1;
    }
    @media screen and (max-width: $sm-break) {
      margin-top: 9px;
    }
  }

  .button-arrow-next, .button-arrow-prev {
    position: absolute;
    z-index: 2;
    top: 0;
    margin-top: 11.5px;
    @media screen and (max-width: $sm-break) {
      display: none;
    }
  }

  .button-label-next, .button-arrow-prev {
    left: 0;
    margin-left: 48px;
    @media screen and (max-width: $sm-break) {
      margin-left: 36px;
    }
  }

  .button-label-prev, .button-arrow-next {
    right: 0;
    margin-right: 48px;
    @media screen and (max-width: $sm-break) {
      margin-right: 36px;
    }
  }

  .button-background-next, .button-background-prev {
    position: absolute;
    z-index: 1;
    pointer-events: none;
    top: 0;
    left: 0;
  }

  .button-background-svg {
    width: 220px;
    height: 48px;
    @media screen and (max-width: $sm-break) {
      width: 120px;
      height: 36px;
      transform: scale(1, 1.25);
    }
  }

  .button-background-path {
    stroke: $blue;
    fill: $white;
    transition: all $transition-1;
  }

</style>

<!-- non-scoped (to traverse to arrow component) -->
<style lang="scss">
  @import '../../styles/variables';
  
  .button-container:hover {
    .arrow-container {
      .fill-group {
        fill: $white;
      }
      .bracket-left {
        transform: translateX(-3px);
      }
      .bracket-right {
        transform: translateX(3px);
      }
    }
  }
</style>


