<template>
  <div> <!-- class="wrapper" -->

    <h2>component sandbox</h2>

    <!-- messing with positioning/spacing -->
    
    <!-- <div class="row debug test-max-height">

      <div class="span-8-md debug test-align">

        <div class="ratio-image-slide debug test-align-inner">
          <h1>Image will go here</h1>
        </div>

      </div>

      <div class="span-8-md relative debug">

        <div class="ratio-square ind-2">
          <h1>Content</h1>
          <p>Lorem Ipsum</p>
        </div>

        <crosshair></crosshair>
        <span-background></span-background>

      </div>

    </div> -->

    <nav-button type="next"></nav-button>
    <nav-button type="prev"></nav-button>

    <!-- card flip -->
    <div class="flip-container debug">
      <div class="flipper">
        <div class="front debug">
          <!-- front content -->
          front
        </div>
        <div class="back debug">
          <!-- back content -->
          back
        </div>
      </div>
    </div>

    <!-- nav -->
    <div class="nav-placeholder debug"></div>

  </div>
</template>

<script>
  import button from '../components/molecules/button';
  export default {
    components: {
      'nav-button': button
    }
  }
</script>

<!-- scoped -->
<style scoped lang='scss'>

  @import '../styles/variables';

  // button container

  .button-container {
    position: relative;
    width: 221px;
    height: 48px;
    cursor: pointer;
    &:hover {
      .utility {
        color: $white;
      }
      .button-background-path {
        stroke: $white;
        fill: $blue;
      }
    }
  }

  .button-label {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    margin-left: 48px;
    margin-top: 14.5px;
    .utility {
      transition: all $transition-1;
    }
  }

  .button-arrow {
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    margin-top: 11.5px;
    margin-right: 48px;
  }

  .button-background-next, .button-background-prev {
    position: absolute;
    z-index: 1;
    pointer-events: none;
    top: 0;
    left: 0;
  }

  .button-background-path {
    stroke: $blue;
    fill: $white;
    transition: all $transition-1;
  }

  .test-max-height {
    max-height: 100%;
    width: 100%;
    margin: auto;
  }

  .test-align {
    display: flex;
  }

  .test-align-inner {
    position: relative;
    width: 100%;
    margin: auto;
  }

  .nav-placeholder {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 96px;
  }

  .wrapper {
    position: relative;
    margin: auto;
    display: flex;
    width: calc(100% - 80px - 80px);
    height: calc(100vh - 60px - 148px);
    margin-top: 60px;
    background: rgba(0, 0, 255, 0.1);
    @media screen and (max-width: $md-break) {
      margin-top: 36px;
      height: auto;
      display: block;
    }
    @media screen and (max-width: $sm-break) {
      margin-top: auto;
      width: 100%;
    }
  }

  //  vendor (see https://davidwalsh.name/css-flip#ieflip)

  /* entire container, keeps perspective */
  .flip-container {
    perspective: 1000px;
    transform-style: preserve-3d;
  }
    /*  UPDATED! flip the pane when hovered */
    .flip-container:hover .back {
      transform: rotateY(0deg);
    }
    .flip-container:hover .front {
        transform: rotateY(180deg);
    }

  .flip-container, .front, .back {
    width: 320px;
    height: 480px;
  }

  /* flip speed goes here */
  .flipper {
    transition: 0.6s;
    transform-style: preserve-3d;

    position: relative;
  }

  /* hide back of pane during swap */
  .front, .back {
    backface-visibility: hidden;
    transition: 0.6s;
    transform-style: preserve-3d;

    position: absolute;
    top: 0;
    left: 0;
  }

  /*  UPDATED! front pane, placed above back */
  .front {
    z-index: 2;
    transform: rotateY(0deg);
  }

  /* back, initially hidden pane */
  .back {
    transform: rotateY(-180deg);
  }

  /* 
    Some vertical flip updates 
  */
  .vertical.flip-container {
    position: relative;
  }

    .vertical .back {
      transform: rotateX(180deg);
    }

    .vertical.flip-container:hover .back {
        transform: rotateX(0deg);
    }

    .vertical.flip-container:hover .front {
        transform: rotateX(180deg);
    }
  

</style>

<!-- non-scoped (to traverse to arrow component) -->
<style lang="scss">
  @import '../styles/variables';
  
  .button-container:hover {
    .arrow-container {
      .fill-group {
        fill: $white;
      }
      .bracket-left {
        transform: translateX(-3px);
      }
      .bracket-right {
        transform: translateX(3px);
      }
    }
  }
</style>


