<template>
  <div class="radio-button-container">
    <input type="checkbox" class="radio-button-checkbox"></input>
    <div class="radio-button" :class="type">
      <div class="radio-button-indicator" :class="type"></div>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['type']
  }
</script>

<style scoped lang="scss">
  @import '../../styles/variables';

  .radio-button-container {
    position: relative;
    margin: 12px;
    width: 24px;
    height: 24px;
  }

  .radio-button-checkbox {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    cursor: pointer;
    width: 100%;
    height: 100%;
    opacity: 0;
    &:hover {
      + .radio-button {
        background-color: transparent;
      }
    }
    &:checked {
      + .radio-button {
        border: 1px solid $blue;
        .radio-button-indicator {
          opacity: 1;
        }
      }
    }
  }

  .radio-button {
    position: relative;
    z-index: 1;
    display: flex;
    width: 100%;
    height: 100%;
    background-color: $light-blue;
    border: 1px solid $light-black;
    border-radius: 50%;
    transition: all $transition-1;
    &.incorrect {
      border: 1px solid $red !important;
    }
    &.correct {
      border: 1px solid $green !important;
    }
  }

  .radio-button-indicator {
    position: relative;
    margin: auto;
    opacity: 0;
    pointer-events: none;
    width: 18px;
    height: 18px;
    background-color: $blue;
    border-radius: 50%;
    transition: all $transition-1;
    &.incorrect {
      opacity: 1;
      background-color: $red !important;
    }
    &.correct {
      opacity: 1;
      background-color: $green !important;
    }
  }
</style>




