<template>

  <div class="hatch-container">

    <!-- background left -->
    <div class="svg-background-left">
      <hatch-bg></hatch-bg>
    </div>

    <!-- background right -->
    <div class="svg-background-right">
      <hatch-bg></hatch-bg>
    </div>

  </div>
  
</template>

<script>
  import hatch_bg from '../svg/hatch_bg'
  export default {
    components: {
      'hatch-bg': hatch_bg
    }
  }
</script>

<style scoped lang='scss'>

  @import '../../styles/variables';

  .hatch-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    background-color: $white;
  }

  .svg-background-left, .svg-background-right {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
    svg {
      position: absolute;
      top: 0;
      margin-top: 1px;  
      width: auto;
      height: 100%;
    }
  }

  .svg-background-left {
    svg {
      left: 0;
      margin-left: 0px;
    }
  }

  .svg-background-right {
    svg {
      right: 0;
      margin-right: -1px;
      transform: scale(-1, 1)
    }
  }

</style>


