<template>
  <div>
    <h2>Atoms</h2>

    <h2>Hatch Background</h2>

    <div class="row debug">
      <div class="span-6-md offset-5-md relative">
        
        <div class="ratio-4-3 ind-2">
          <h1>Content</h1>
          <p>Lorem Ipsum</p>
        </div>

        <span-background></span-background>
      </div>
    </div>

    <h2>Gradient Background</h2>
    
    <div class="row">
      <div class="span-6-md offset-5-md bg-gradient ratio-4-3"></div>
    </div>

    <h2>Crosshair Background</h2>

    <div class="row">
      <div class="span-6-md offset-5-md relative">

        <div class="ratio-4-3 debug">
          <h1>Content</h1>
          <p>Lorem Ipsum</p>
        </div>

        <crosshair></crosshair>
      </div>
    </div>

    <h2>Combined Hatch/Crosshair</h2>

    <div class="row debug">
      <div class="span-6-md offset-5-md relative">

        <div class="ratio-4-3 ind-2">
          <h1>Content</h1>
          <p>Lorem Ipsum</p>
        </div>

        <crosshair></crosshair>
        <span-background></span-background>

      </div>
    </div>

    <h2>Icons</h2>

    <access-to-services></access-to-services>
    <celebrate></celebrate>
    <collaborate></collaborate> etc.

    <h2>Radio Buttons</h2>

    <radio></radio>
    <radio type="incorrect"></radio>
    <radio type="correct"></radio>

    <h2>Arrows</h2>
    <arrow type="next"></arrow>
    <arrow type="prev"></arrow>
    <arrow type="plus"></arrow>

  </div>
</template>

<script>

  // atoms
  import span_background from '../../components/atoms/span_background'
  import crosshair_background from '../../components/atoms/crosshair_background'
  import radio from '../../components/atoms/radio'
  import arrow from '../../components/atoms/arrow'

  // svg
  import access_to_services from '../../components/svg/access_to_services'
  import celebrate from '../../components/svg/celebrate'
  import collaborate from '../../components/svg/collaborate'

  export default {
    components: {
      'span-background': span_background,
      'crosshair': crosshair_background,
      'radio': radio,
      'arrow': arrow,

      // svg
      'access-to-services': access_to_services,
      'celebrate': celebrate,
      'collaborate': collaborate,
      // ...etc. potentially will manage these internal or in ACF -> ie. set as background image during render from props
    }
  }
</script>

<style scoped lang="scss">
  h2 {
    margin: 72px auto;
    text-align: center;
  }
</style>


